import { getUsers } from '@apis/users'
import { STATE, STATE_MAP } from '@constants/state'
import { Dropdown, Menu, Tag, Avatar, Popover } from 'antd'
import { Component } from 'react'
import { find, filter, remove } from 'lodash'
import { CheckOutlined, PlusOutlined, UserOutlined } from '@ant-design/icons'
import styles from './index.module.less'

type Props = {
  selectIds?: string
  callBack?: ($event) => void
}

type State = {
  members: any
  selectMembers: any
}

export default class SelectMembersComponent extends Component<Props, State> {
  constructor(props) {
    super(props)
    this.state = {
      members: [],
      selectMembers: [],
    }
  }
  async componentDidMount() {
    this.getMembersList()
  }

  async getMembersList() {
    const { data } = await getUsers()
    const { selectIds } = this.props
    let smbs = []
    if (selectIds && selectIds.length > 0) {
      smbs = filter(data, (n) => {
        const isHas = selectIds.includes(n._id)
        n.checked = isHas
        return isHas
      })
    }
    this.setState({
      members: data,
      selectMembers: smbs,
    })
  }

  componentWillReceiveProps(nextProps) {
    // if (nextProps.selectIds !== this.props.selectIds) {
    //   this.getMembersList()
    // }
  }

  selectActions(mb) {
    const smb = find(this.state.selectMembers, { _id: mb._id })
    const ss = find(this.state.members, { _id: mb._id })
    if (!smb) {
      ss.checked = true
      this.state.selectMembers.push(mb)
    } else {
      ss.checked = false
      remove(this.state.selectMembers, { _id: mb._id })
    }
    this.setState({
      members: [...this.state.members],
      selectMembers: [...this.state.selectMembers],
    })
  }

  setVisible(visible) {
    if (!visible) {
      // 隐藏 保存
      const { callBack } = this.props
      callBack && callBack(this.state.selectMembers)
    }
  }

  openPopMembers() {}

  render() {
    const { members, selectMembers } = this.state
    const popMembers = (
      <div>
        {members.map((mb) => {
          return (
            <div
              className={styles['member-item']}
              onClick={() => {
                this.selectActions(mb)
              }}
            >
              <div className={styles['member-item-content']}><Avatar style={{background:'#2249c0'}}>{mb.username}</Avatar> <span className="m-l-8">{mb.username}</span></div>
              {mb.checked ? <CheckOutlined /> : null}
            </div>
          )
        })}
      </div>
    )
    return (
      <div className={styles['members-wrap']}>
        <Avatar.Group maxCount={2} maxStyle={{ color: '#f56a00', backgroundColor: '#fde3cf' }}>
          {selectMembers.map((smb) => {
            return <Avatar style={{background:'#2249c0'}}>{smb.username}</Avatar>
          })}
        </Avatar.Group>
        <Popover
          placement="bottom"
          onVisibleChange={(visible) => {
            this.setVisible(visible)
          }}
          title={'选择人员'}
          content={popMembers}
          trigger="click"
        >
          <Avatar>
            <PlusOutlined
              onClick={() => {
                this.openPopMembers()
              }}
            />
          </Avatar>
        </Popover>
      </div>
    )
  }
}
